import React from "react"
import { graphql } from "gatsby"
import { StaticImage } from "gatsby-plugin-image"
import { css } from "@emotion/react"
import Layout from "../../components/layout"
import ColumnLink from "../../components/column-link"
import Seo from "../../components/seo"
import "../../styles/main-privacy.css"

export default function Home({ data }) {
  return (
    <Layout>
      <Seo title="コラム" />
      <section class="wrap privacy">
        <p
          css={css`
            font-size: 25px;
            font-weight: 400;
          `}
        >
          只今、準備中です。
        </p>
        <StaticImage 
          src="../../images/under-construct.jpg"
          alt="工事中"
          placeholder="blurred"
          css={css`
            margin: 50px 0px 110px 0;
          `}
        />
      </section>
    </Layout>
  )
}

export const query = graphql`
  query allContentfulPost {
    allContentfulColumn {
      edges {
        node {
          title
          image {
            title
            file {
              url
            }
            gatsbyImageData(width: 200)
          }
          excerpt {
            excerpt
          }
          slug
          updatedAt( formatString: "YYYY-MM-DD")
        }
      }
    }
  }
`